.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f7f9fb;
  transition: opacity 2.2s, visibility 2.2s;
}

.loader-hidden {
  opacity: 0;
  visibility: hidden;
}

.loader div {
  transform-origin: 40px 40px;
  animation: loader 2.2s linear infinite;
}

.loader div:after {
  content: " ";
  display: block;
  position: absolute;
  top: 3px;
  left: 37px;
  width: 6px;
  height: 18px;
  border-radius: 20%;
  background: #FF6B08;
}

.loader div:nth-child(1) {
  transform: rotate(0deg);
  animation-delay: -2.1s;
}

.loader div:nth-child(2) {
  transform: rotate(30deg);
  animation-delay: -2s;
}

.loader div:nth-child(3) {
  transform: rotate(60deg);
  animation-delay: -1.9s;
}

.loader div:nth-child(4) {
  transform: rotate(90deg);
  animation-delay: -1.8s;
}

.loader div:nth-child(5) {
  transform: rotate(120deg);
  animation-delay: -1.7s;
}

.loader div:nth-child(6) {
  transform: rotate(150deg);
  animation-delay: -1.6s;
}

.loader div:nth-child(7) {
  transform: rotate(180deg);
  animation-delay: -1.5s;
}

.loader div:nth-child(8) {
  transform: rotate(210deg);
  animation-delay: -1.4s;
}

.loader div:nth-child(9) {
  transform: rotate(240deg);
  animation-delay: -1.3s;
}

.loader div:nth-child(10) {
  transform: rotate(270deg);
  animation-delay: -1.2s;
}

.loader div:nth-child(11) {
  transform: rotate(300deg);
  animation-delay: -1.1s;
}

.loader div:nth-child(12) {
  transform: rotate(330deg);
  animation-delay: -1s;
}

.loader div:nth-child(13) {
  transform: rotate(60deg);
  animation-delay: -0.9s;
}

.loader div:nth-child(14) {
  transform: rotate(90deg);
  animation-delay: -0.8s;
}

.loader div:nth-child(15) {
  transform: rotate(120deg);
  animation-delay: -0.7s;
}

.loader div:nth-child(16) {
  transform: rotate(150deg);
  animation-delay: -0.6s;
}

.loader div:nth-child(17) {
  transform: rotate(180deg);
  animation-delay: -0.5s;
}

.loader div:nth-child(18) {
  transform: rotate(210deg);
  animation-delay: -0.4s;
}

.loader div:nth-child(19) {
  transform: rotate(240deg);
  animation-delay: -0.3s;
}

.loader div:nth-child(20) {
  transform: rotate(270deg);
  animation-delay: -0.2s;
}

.loader div:nth-child(21) {
  transform: rotate(300deg);
  animation-delay: -0.1s;
}

.loader div:nth-child(22) {
  transform: rotate(330deg);
  animation-delay: 0s;
}

@keyframes loader {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}