.is-hidden {

    opacity: 0;
    visibility: visible;
    pointer-events: none;
}



.modal {
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translate(-50%, 0%);
    border: 1px #000000 solid;
    background-color: #fff;
    margin: 0 auto;
    // max-width: 280px;
    padding: 48px 20px 40px;
    opacity: 1;
    z-index: 2;
    display: flex;

    &__close-btn {
        width: 12px;
        height: 12px;

        &--icon {
            width: 12px;
            height: 12px;
        }
    }

    &__head {
        margin-top: 30px;
    }

    &__img {

        min-width: 240px;
        min-height: 350px;
        border: 1px solid grey;

        &--picture {
            display: block;

        }
    }

    &__list {

        display: flex;
        flex-direction: column;
        margin-top: 20px;
    }

    &__item {
        font-size: 12px;
        line-height: 16px;
        display: flex;
        margin-bottom: 10px;
        justify-content: space-between;


    }

    &__parameters {
        color: #8c8c8c;
        font-weight: 500px;
        font-size: 12px;
        line-height: 16px;

    }

    &__about {
        margin: 20px auto 10px 0px;
        text-transform: uppercase;
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
    }

    &__text {
        font-weight: 500;
        font-size: 12px;
        line-height: 20px;
    }

    &__button-container {
        margin-top: 21px;
        display: flex;
        justify-content: space-between;
    }

    &__buttons {

        background-color: transparent;
        width: 112px;
        height: 44px;
        border: 1px solid #000000;
        border-radius: 5px;
        transition: color $transitionEffect, background-color $transitionEffect, border-color $transitionEffect;

        &:hover {
            color: #fff;
            border-color: $mainColor;
            background-color: $mainColor;
        }
    }

    &__rating {
        display: inline-block;
        text-align: center;
        color: #fff;
        width: 36px;
        height: 16px;
        background-color: $mainColor;
        border-radius: 5px;



    }

    &__votes-number {
        display: inline-block;
        text-align: center;
        color: black;
        width: 36px;
        height: 16px;
        background-color: #f7f7f7;
        border-radius: 5px;
    }
}