body {
  font-family: $mainFont, "Roboto", sans-serif;
  color: $mainFontColor;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
  list-style-type: none;
}

p {
  padding: 0;
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
a{
text-decoration: none;
color:inherit;}

a:visited{
  color: inherit;
}
